import {
  Component,
  Input,
  Output,
  EventEmitter,
  ContentChild,
  ContentChildren,
  QueryList,
  AfterContentInit,
  OnDestroy,
  NgModule,
  ModuleWithProviders,
  ViewEncapsulation,
  ChangeDetectionStrategy
} from '@angular/core';
import {VdlCheckboxModule} from '../checkbox/checkbox';

import 'rxjs/add/operator/share';
import {
  VdlDataTableHeaderSelectableRow,
  VdlDataTableSelectableRow,
  ITableSelectableRowSelectionChange
} from './data-table-selectable-tr';

/**
 * Selectable change event data
 */
export interface ITableSelectionChange {
  name: string;
  allSelected: boolean;
  values: any[];
}

@Component({
  moduleId: module.id,
  selector: 'vdl-data-table',
  templateUrl: 'data-table.html',
  styleUrls: ['data-table.css'],
  host: {
    '[class.vdl-data-table]': 'true',
    '[class.vdl-data-table-selectable]': 'selectable',
  },
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class VdlDataTable implements AfterContentInit, OnDestroy {
  @Input()
  selectable: boolean;
  @Output()
  onSelectableAll: EventEmitter<any> = new EventEmitter(false);
  @Output()
  onSelectableChange: EventEmitter<any> = new EventEmitter(false);

  @ContentChild(VdlDataTableHeaderSelectableRow)
  _masterRow: VdlDataTableHeaderSelectableRow;

  @ContentChildren(VdlDataTableSelectableRow, true)
  _rows: QueryList<VdlDataTableSelectableRow>;

  _listeners: EventEmitter<any>[] = [];

  selected: Array<string> = [];

  constructor() {
    this.onSelectableChange.share();
  }

  change(event: ITableSelectableRowSelectionChange) {
    let outputEvent: ITableSelectionChange = {
      name: 'selectable_change',
      allSelected: false,
      values: []
    };
    if (event.target instanceof VdlDataTableHeaderSelectableRow === true) {
      if (event.isActive === true) {
        outputEvent.allSelected = true;
        outputEvent.values = this._getRowsValues();
      }
    } else {
      outputEvent.values = this.selected.slice(0);

      if (event.isActive === true) {
        outputEvent.values.push(event.selectableValue);
      } else {
        let index = outputEvent.values.indexOf(event.selectableValue);
        if (index !== -1) {
          outputEvent.values.splice(index, 1);
        }
      }
    }

    // dispatch change
    this.selected = outputEvent.values;
    this.onSelectableChange.emit(outputEvent);
  }

  /**
   * @returns {Array<string>}
   */
  _getRowsValues(): any[] {
    return this._rows.toArray()
      .map((tr: VdlDataTableSelectableRow) => tr.selectableValue);
  }

  _unsubscribeChildren() {
    this.selected = [];
    if (this._listeners.length) {
      this._listeners.forEach(listener => {
        listener.unsubscribe();
      });
      this._listeners = [];
    }
  }

  _updateChildrenListener(list: QueryList<VdlDataTableSelectableRow>) {
    this._unsubscribeChildren();

    list.toArray()
      .map((tr: VdlDataTableSelectableRow) => {
        tr.onChange.subscribe(this.change.bind(this));
      });
  }

  ngAfterContentInit() {
    if (this.selectable === true) {
      if (!!this._masterRow) {
        this._masterRow.onChange.subscribe(this.change.bind(this));
      }

      this._rows.changes.subscribe(this._updateChildrenListener.bind(this));
      this._updateChildrenListener(this._rows);
    }
  }

  ngOnDestroy() {
    this._unsubscribeChildren();
  }

}

@NgModule({
 declarations: [
   VdlDataTable,
   VdlDataTableHeaderSelectableRow,
   VdlDataTableSelectableRow
 ],
 exports: [
   VdlDataTable,
   VdlDataTableHeaderSelectableRow,
   VdlDataTableSelectableRow
 ],
 imports: [
   VdlCheckboxModule
 ]
})
export class VdlDataTableModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: VdlDataTableModule,
      providers: []
    };
  }
}
